  /* displaying the last three bullets when slick-active class isn't applied to any li before them  */
  /* hiding the last three bullets if slick-active exist before them */
  /* specific conditions to always display the last three bullets */ }
  .article-slider .slick-dots {
    position: absolute;
    bottom: 12px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center; }
  .article-slider .dotscustom {
    width: 30px;
    height: 30px;
    background: transparent;
    border: none;
    border-radius: 50%;
    margin: 0 5px;
    cursor: pointer;
    position: relative; }
    .article-slider .dotscustom:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 6px;
      height: 6px;
      background-color: #ffffff;
      border-radius: 50%; }
  .article-slider .slick-active .dotscustom {
    border: 1px solid #ffffff; }
  .article-slider .slick-dots li {
    display: none; }
  .article-slider .slick-dots li.slick-active,
  .article-slider .slick-dots li.slick-active + li,
  .article-slider .slick-dots li.slick-active + li + li {
    display: block; }
  .article-slider .slick-dots li:nth-last-child(1),
  .article-slider .slick-dots li:nth-last-child(2),
  .article-slider .slick-dots li:nth-last-child(3) {
    display: block; }
  .article-slider .slick-dots li.slick-active ~ li:nth-last-child(1),
  .article-slider .slick-dots li.slick-active ~ li:nth-last-child(2),
  .article-slider .slick-dots li.slick-active ~ li:nth-last-child(3) {
    display: none; }
  .article-slider .slick-dots li.slick-active + li + li:nth-last-child(3),
  .article-slider .slick-dots li.slick-active + li + li:nth-last-child(2),
  .article-slider .slick-dots li.slick-active + li + li:nth-last-child(1),
  .article-slider .slick-dots li.slick-active + li:nth-last-child(3),
  .article-slider .slick-dots li.slick-active + li:nth-last-child(2),
  .article-slider .slick-dots li.slick-active + li:nth-last-child(1) {
